import React from "react"
import Layout from "../components/layout"
import "../styles/global.scss"
import { graphql, useStaticQuery, Link } from "gatsby"
import { Helmet } from "react-helmet"

export default function NotFound() {
  const data = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
          }
        }
      }
    `
  )
  return (
    <div>
      <Helmet>
        <title>404 - Not found | {data.site.siteMetadata.title}</title>
        <meta name="description" content="Page not found" />
        <link rel="canonical" href={data.site.siteMetadata.url + "/404.html"} />
        <meta property="og:title" content={"404 - Not found | " + data.site.siteMetadata.title} />
        <meta property="og:description" content="Page not found" />
        <meta property="og:image" content={data.site.siteMetadata.img} />
        <meta property="og:url" content={data.site.siteMetadata.url + "/404.html"} />
      </Helmet>
      <Layout>
        <article>
          <section id="intro">
            <h2>Whoops.</h2>
            <p>Looks, like you're lost. You might want to mosey on over to the <Link to="/">home page</Link>.</p>
            <img src="https://media.giphy.com/media/3o6Mbj3KnAvvgbMvJK/giphy.gif" alt="Bart Simpson: 'Well, gotta mosey' in cowboy hat." />
          </section>
        </article>
      </Layout>
    </div>
  )
}

